.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.658);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  max-width: 1366px;
  min-height: 768px;
}

.modal img {
  width: 100%;
  height: 100vh;
}
